  // tslint:disable-next-line:use-life-cycle-interface
  ngAfterViewInit(): void {

    const description = document.getElementById('desc');
    if (description == null) {
      return;
    }
    const all = document.getElementById('all');
    const descString = description.textContent;
    const allString = all.textContent;
    const index = allString.indexOf('/');
    let showString = '';
    let hideString = '';
    if (index > 0) {
      showString = allString.substr(0, index);
      hideString = allString.substr(index + 1, allString.length);
    }

    const n = description.offsetHeight;
    if (n < 1) {
      all.style.display = 'none';
      description.style.height = '0';
    }

    for (let i = 0; i < descString.length; i++) {
      description.innerHTML = descString.substr(0, i);
      if (n < description.scrollHeight) {
        description.style.overflow = 'hidden';
        description.innerHTML = descString.substr(0, i - 3) + '...';
        all.style.display = 'block';
        all.innerHTML = showString;
        description.style.height = '110px';
        break;
      } else {
        description.innerHTML = descString;
        all.style.display = 'none';
        description.style.height = 'auto';
      }
    }

    all.onclick = function() {
      if (description.style.overflow === 'hidden') {
        description.innerHTML = descString;
        description.style.overflow = 'auto';
        description.style.height = 'auto';
        all.innerHTML = hideString;
      } else {
        for (let i = 0; i < descString.length; i++) {
          description.innerHTML = descString.substr(0, i);
          if (n < description.scrollHeight) {
            description.style.overflow = 'hidden';
            description.style.height = '110px';
            description.innerHTML = descString.substr(0, i - 3) + '...';
            all.innerHTML = showString;
            break;
          }
        }
      }
    };

  }

